nav.navbar.navbar-default {
  background-color: rgba(255, 255, 255, 0.32);
  border: none;
  color: #fff;
}

nav.navbar.navbar-default a {
  color: #fff;
}

nav.navbar.navbar-default a:hover {
  color: #fff;
  opacity: 0.8;
}

.navbar-header {
  position: absolute;
  left: 10%;
  z-index: 99;
  flex: 1;
}

.navbar-header .navbar-brand {
  color: #fff;
}

.navbar-header .navbar-brand:hover {
  color: #fff;
  opacity: 0.8;
}

.nav-center {
  z-index: 1;
  position: relative;
  text-align: center;
  margin-top: 14px;
  font-size: 1.6rem;
  flex: 2;
}

.nav-center span {
  margin-left: 10px;
}

.nav-right {
  z-index: 99;
  position: absolute;
  right: 40px;
  padding: 10px;
}

.nav-right img {
  width: 24px;
  margin-top: 4px;
  border-radius: 30px;
}

.dropdown-menu {
  left: -75px;
  background: rgba(238, 238, 238, 0.24);
  padding: 10px;
  text-align: center;
}
